<template lang="pug">
    .new__department-content
        .department__title Департамент
        form(@submit.prevent="saveDepartment").new__department
            .new__department-points
                .form__row-department
                    .form__item-department
                        img(src='../../../assets/images/icons/owl2.svg').img__department
                    .form__item-department
                        label.label__name-department Название
                            input(type='text' v-model='selectedName').new__department__name
                .buttons_new-department
                    button(type='submit').save__button-department Сохранить
                    button(@click.prevent='resetDepartment').reset__button-department Отмена
            
</template>
<script>
    import { mapActions } from "vuex";
export default {
    data() {
        return {
            selectedName: ''
        }
    },
    methods: {
        ...mapActions("departments", ["addDepartment"]),
        ...mapActions("error", ["fetchError"]),
        saveDepartment() {
            if(this.selectedName==''){
                this.fetchError('Не все поля заполнены!')
            }
            else{
                this.addDepartment(this.selectedName)
                this.$emit('eventNewDepartment')
            }
        },
        resetDepartment() {
            this.$emit('eventNewDepartment')
        }
    }
}
</script>
